<template>
  <div id="login_box">
    <el-card class="box-card">
      <h3 class="textCenter">系统登录</h3>

      <el-input
        class="accInput"
        prefix-icon="el-icon-user-solid"
        placeholder="请输入用户名"
        v-model="acc"
        clearable
      ></el-input>
      <el-input
        class="pwdInput"
        prefix-icon="el-icon-lock"
        placeholder="请输入密码"
        v-model="pwd"
        show-password
      ></el-input>
      <el-button @click="clickLogin" class="loginBtn" type="primary">登录</el-button>
    </el-card>
  </div>
</template>

<script>
import { API_LOGIN,API_GET_MYINFO } from "@/API/apis.js";

export default {
  data() {
    return {
      acc: "",
      pwd: "",
      nextClick: true //防抖节流判断
    };
  },
  methods: {
    clickLogin() {
      //  防抖节流，点一次发送请求后，需要等三秒才能发送下一次
      if (this.nextClick == false) return; //如果是false则不发送
      // 把状态变成false
      this.nextClick = false;
      // 发送请求
      API_LOGIN(this.acc, this.pwd).then(res => {
        // console.log(res.data);s
        //如果返回码是0，登录成功
        if (res.data.code == 0) {
          //console.log(res)
          // 保存用户ID在本地存储
          localStorage.id=res.data.id
          // 保存token到本地
          localStorage.token=res.data.token
          // 保存用户名
          localStorage.acc=this.acc
          // 保存用户组
          localStorage.role=res.data.role
          // 发送请求获取用户信息（头像）
          API_GET_MYINFO(localStorage.id).then(userinfo=>{
            //console.log(userinfo);
            localStorage.head=userinfo.data.accountInfo.imgUrl
          })
          this.$router.push("/index/Home"); //改变路由地址
          this.$message({
            message: "登录成功",
            type: "success",
            duration: 1500
          });
        } else {
          this.$message.error("用户名或密码错误");
        }
      });
      setTimeout(() => {
        this.nextClick = true; //三秒后把状态变为true，下次可以发送请求了
      }, 3000);
    }
  }
};
</script>

<style lang="less" scoped>
@basecolor: #2d3a4b;
// 登录页面背景样式
#login_box {
  width: 100%;
  height: 100%;
  -background-color: @basecolor;
  background-image:url(../assets/imgs/bckimg.jpg);
  background-size: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .box-card {
    //   登录页面卡片样式设置
    background-color: rgba(0, 0, 0, 0.315);
    border: none;
    width: 400px;
    box-shadow: none;
    //   用户/密码/登录元素样式设置
    .accInput {
      margin-bottom: 10px;
      border-color: red;
    }
    .pwdInput {
      margin-bottom: 15px;
    }
    .loginBtn {
      width: 100%;
      letter-spacing: 20px;
    }
    .textCenter {
      text-align: center;
      margin-top: 0;
      color: #fff;
    }
  }
}
</style>