import Vue from 'vue';
import VueRouter from 'vue-router';
import Login from '../pages/Login'
import {
    API_CHECK_TOKEN
} from '../API/apis';

//  router和Vue工程联系
Vue.use(VueRouter);

// 创建实例对象
const router = new VueRouter({
    routes: [
        // 登录页
        {
            //配置路由切换规则
            path: '/',
            name: 'Login',
            component: Login
        },
        // 首页一级路由
        {
            path: '/index',
            name: 'index',
            component: () => import('../pages/Index'),
            // 首页二级路由
            children: [
                // 订单管理
                {
                    path: '/index/order',
                    name: 'order',
                    component: () => import('../pages/index/Order'),
                    meta: ['super', 'normal']
                },
                // 店铺管理
                {
                    path: '/index/storemana',
                    name: "storemana",
                    component: () => import('../pages/index/StoreMana'),
                    meta: ['super']
                },
                // 商品统计
                {
                    meta: ['super'],
                    path: '/index/shopstatistics',
                    name: "shopstatistics",
                    component: () => import('../pages/index/Shopstatistics')
                },
                // 商品列表
                {
                    meta: ['super', 'normal'],
                    path: '/index/shoplist',
                    name: "shoplist",
                    component: () => import('../pages/index/Shoplist')
                },
                // 商品分类
                {
                    meta: ['super', 'normal'],
                    path: '/index/shopclassify',
                    name: "shopclassify",
                    component: () => import('../pages/index/Shopclassify')
                },
                // 订单统计
                {
                    meta: ['super'],
                    path: '/index/orderstatistics',
                    name: "orderstatistics",
                    component: () => import('../pages/index/Orderstatistics')
                },
                // 修改密码
                {
                    meta: ['super'],
                    path: '/index/editpwd',
                    name: "editpwd",
                    component: () => import('../pages/index/Editpwd')
                },
                // 添加用户
                {
                    meta: ['super'],
                    path: '/index/adduser',
                    name: "adduser",
                    component: () => import('../pages/index/Adduser')
                },
                // 添加商品
                {
                    meta: ['super', 'normal'],
                    path: '/index/addshops',
                    name: "addshops",
                    component: () => import('../pages/index/Addshops')
                },
                // 个人中心
                {
                    meta: ['super', 'normal'],
                    path: '/index/myinfo',
                    name: "myinfo",
                    component: () => import('../pages/index/Myinfo')
                },
                // 首页
                {
                    meta: ['super', 'normal'],
                    path: '/index/home',
                    name: "home",
                    component: () => import('../pages/index/Home')
                },
                // 用户列表
                {
                    meta: ['super'],
                    path: '/index/userlist',
                    name: "userlist",
                    component: () => import('../pages/index/Userlist')
                }
            ]
        },

    ]
})

// 启用路由守卫
router.beforeEach((to, from, next) => {
    from;
    // 只要跳转的路径不是登录页面，都需要路由拦截，验证token
    if (to.path != '/') {
        API_CHECK_TOKEN(localStorage.token).then(res => {
            if (res.data.code == 0) { //如果token有效
                next() //放行，进行下一步操作
                // 如果权限名单里有当前用户组
                if (to.meta.includes(localStorage.role)) {
                   // console.log(1)
                    next() //放行
                } else {
                    // 否则刷新页面
                    next(from.path);             
                }
            } else {
                // 拦截路由跳转到首页
                next('/')
            }
        })
    } else {
        next()
    }
})


export default router
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}