import axios from 'axios';
// 设置服务器IP地址
export const SERVER_IP = "http://127.0.0.1:5000"
// 上传图片API地址
export const SERVER_UPLOAD = SERVER_IP + '/shop/upload'
// 店铺图片地址
export const SERVER_SHOP_IMG = SERVER_IP + '/upload/shop/'
axios.defaults.baseURL = SERVER_IP
// 登录API
// account：用户名  password：密码
export const API_LOGIN = (account, password) => axios.post("/users/checkLogin", {
    account,
    password
})

// 添加账号
// account：用户名  password：密码 userGroup:用户分组
export const API_ADD_ACCOUNT = (account, password, userGroup) => axios.post("/users/add", {
    account,
    password,
    userGroup
})

// 获取账号列表
// currentPage:当前页    pageSize：每页显示条数
export const API_GET_USERLIST = (currentPage, pageSize) => axios.get("/users/list", {
    params: {
        currentPage,
        pageSize
    }
})


// 删除账号数据
// id:用户id
export const API_DELETE_ACC = (id) => axios.get("/users/del", {
    params: {
        id
    }
})


// 批量删除账号
// ids：id组成的数组 
export const API_DELETE_CHECKED = (ids) => axios.get('/users/batchdel', {
    params: {
        ids
    }
})

// 编辑账号
// /users/edit  id：用户id  account：账号  userGroup：用户组
export const API_EDIT_ACCOUNT = (id, account, userGroup) => axios.post("/users/edit", {
    id,
    account,
    userGroup
})


// 检测旧密码
// oldPwd：旧密码  id
export const API_CHECK_PWD = (oldPwd, id) => axios.get("/users/checkoldpwd", {
    params: {
        oldPwd,
        id
    }
})

// 修改密码
// newPwd：新密码 id:用户ID
export const API_EDIT_PWD = (newPwd, id) => axios.post('/users/editpwd', {
    newPwd,
    id
})

// 获取个人信息
//id：用户id
export const API_GET_MYINFO = (id) => axios.get("/users/accountinfo", {
    params: {
        id
    }
})

//验证token时效
//token:用户注册时，服务器生成的随机字符串，用于用户验证
export const API_CHECK_TOKEN = (token) => axios.get("/users/checktoken", {
    params: {
        token
    }
})

// 获取商品分类
// currentPage:当前页    pageSize：每页显示条数
export const API_GET_SHOP_CLASSIFY = (currentPage, pageSize) => axios.get('/goods/catelist', {
    params: {
        currentPage,
        pageSize
    }
})


// 添加商品分类
// cateName:分类名称 state:启用状态
export const API_ADD_SHOP_CLASSIFY = (cateName, state) => axios.post("/goods/addcate", {
    cateName,
    state
})


// 删除商品分类
// id：商品id
export const API_DELETE_SHOP_CLASSIFY = (id) => axios.get("/goods/delcate", {
    params: {
        id
    }
})


// 修改分类  ************************
// id 商品分类id  cateName分类名称
export const API_EDIT_SHOP_CLASSIFY = (id, cateName) => axios.post("/goods/editcate", {
    id,
    cateName
})


//查询所有分类名称
///goods/goods_img_upload
export const API_GET_ALL_SHOP_CLASSIFY = () => axios.get("/goods/categories")


// 获取首页报表
export const API_GET_HOME_ECHARTS = () => axios.get("/order/totaldata")

// 添加商品
// name：商品名称 category：商品分类 pirce：价格 imgUrl：商品图片地址 goodsDesc：商品描述
export const API_ADD_SHOPS = (name, category, price, imgUrl, goodsDesc) => axios.post("/goods/add", {
    name,
    category,
    price,
    imgUrl,
    goodsDesc
})

//获取商品列表
// currentPage:当前页    pageSize：每页显示条数
export const API_GET_SHOP_LIST = (currentPage, pageSize) => axios.get('/goods/list', {
    params: {
        currentPage,
        pageSize
    }
})

// 删除商品
// id:要删除商品的id
export const API_DELETE_SHOP = (id) => axios.get("/goods/del", {
    params: {
        id
    }
})


// 修改商品信息（商品列表）
//name：商品名称 category：商品分类 price：商品价格 imgUrl：商品图片地址 goodsDesc：商品描述 id：商品id
export const API_EDIT_SHOP = (name, category, price, imgUrl, goodsDesc, id) => axios.post("/goods/edit", {
    name,
    category,
    price,
    imgUrl,
    goodsDesc,
    id
})


// 获取订单列表
// currentPage:当前页    pageSize：每页显示条数 orderNo:订单号 consignee：收货人  phone：手机 orderState：订单状态 data:日期
export const API_GET_ORDER_LIST = (currentPage, pageSize, orderNo, consignee, phone, orderState, date) => axios.get("/order/list", {
    params: {
        currentPage,
        pageSize,
        orderNo,
        consignee,
        phone,
        orderState,
        date
    }
})


// 订单列表查询
// currentPage:当前页    pageSize：每页显示条数,orderNo:订单号,consignee：收货人,phone：手机 orderState：订单状态 data:日期
export const API_ORDER_QUERY = (currentPage, pageSize, orderNo, consignee, phone, orderState, date) => axios.get("/order/search", {
    params: {
        currentPage,
        pageSize,
        orderNo,
        consignee,
        phone,
        orderState,
        date
    }
})

// 获取商品详情
// id:商品id
export const API_GET_ORDER_INFO = (id) => axios.get("/order/detail", {
    params: {
        id
    }
})


// 修改订单
// id:要修改订单的id orderNo：订单号 orderTime：下单时间 phone：手机 consignee：收货人 deliverAddress：收货地址 deliverTime：送达时间 remarks：备注 orderAmount：订单金额 orderState：订单状态
export const API_EDIT_ORDER = (id, orderNo, orderTime, phone, consignee, deliverAddress, deliveryTime, remarks, orderAmount, orderState) => axios.post("/order/edit", {
    id,
    orderNo,
    orderTime,
    phone,
    consignee,
    deliverAddress,
    deliveryTime,
    remarks,
    orderAmount,
    orderState
})

// 获取店铺信息
export const API_GET_STROE_INFO = () => axios.get("/shop/info")

// 店铺内容修改
export const API_EDTI_STORE = (params) => axios.post("/shop/edit", params)


// 订单报表接口
// /order/ordertotal date:字符串格式数组
export const API_GET_ORDER_REPORT=(date)=>axios.get("/order/ordertotal",{params:{date}})